/* selected link blue */
/*! .pagination styling */

.pagination .active a {
  color: #ffffff;
  background-color: crimson!important;
  border-color: white!important;
  cursor: default;
}

.pagination{
  padding-bottom: 1.5rem!important;
  margin-bottom: 0rem!important;
}

/*======================================== home component styling=================================== */
.home-img{
  z-index: -10;
}
.crousel-img{
  height: 613px!important;
}
.home-img::before{
  position:absolute!important;
  content:'';
  height:100%!important;
  width:100%!important;
  right:0!important;
  top:0!important;
  background:black!important;
  z-index:1!important;
  opacity: 0.75;
}

.home-container{
  display: flex;
  align-items: center;
  justify-content: center;
}
.home-container  h1{
  position: absolute;
  z-index: 10;
  font-size: 70px;
color: #fff;
text-align: center;
}



/* 
 ============================================filter component styling ==========================*/

 .movies-container{
   /* background: #050811; */
   background: #000000;
   padding: 50px;

 }

 .movies-container h1{
  color: #fff;
text-transform: uppercase;
padding: 10px 0;
}

.movies-container .filter-section {
  background: #0f0f0f;
  color: #fff;
  padding: 30px 35px;
  max-height: 1000px!important;
  min-width: 300px!important;
}

/* .filter-responsive{
  display: block!important;
} */
.filter-section .filter-content h3 {
/* padding: 10px 30px; */
}
.filter-section .filter-content ul{
    padding:0px;
  }
.filter-section .filter-content ul li{
  
list-style: none;
padding: 5px;
}
#filter-toggle{
  display: none;
}

.filter-section .filter-content ul li:last-child,.one-star{
  border-bottom: 1px solid white;
  width: 100%;
  padding-bottom: 20px;
  }

.filter-section .filter-content ul li button,.categories button{
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  background:transparent;
  border: none;
  outline: none;
  transition: all 0.3s ease;
  }

  .filter-section .filter-content ul li  button:hover,.categories button:hover{
   color:#BB2D3B
    }
 
    .filter-content .rating {
      cursor: pointer;
      }

  .filter-content .rating span{
    padding: 5px 3px;
    }
.filter-content .rating .checked{
color:goldenrod;
}

