/* media  qureies */
/*================= header=================  */
@media (max-width:992px) {
  .nav-link{
    color: #000000!important;
  }
  .navbar-collapse{
    background-color: #f8f9fa!important
  }
}
/*================= home=================  */



@media (max-width:992px) {

  .crousel-img{
    height: 420px!important;
  }
  .home-container  h1{
    font-size: 55px;
  }  
}
@media (max-width:850px) {

  .home-container  h1{
    font-size: 45px;
  }  
}
@media (max-width:768px) {
 
  .crousel-img{
    height: 300px!important;
  }

  .home-container h1{
    margin-bottom: 20px;
    font-size: 40px;
  }


  @media (max-width:605px) {
    .home-container  h1{
      font-size: 32px;      
   }
   
 }

  @media (max-width:477px) {
    .home-container   h1{
       font-size: 27px;      
    }
    
  }
  @media (max-width:400px) {
    .home-container   h1{
      font-size: 23px;      
   }
 
  }
}

